"use client";

import React, { useState, useRef, useEffect } from "react";

interface Slide {
  page: number;
  image_base64: string;
  text: string;
}

export default function Home() {
  const [slides, setSlides] = useState<Slide[]>([]);
  const [current, setCurrent] = useState(0);
  const isTransitioning = useRef(false);
  const lastScrollTime = useRef<number>(0);
  const touchStartY = useRef<number | null>(null);
  const accumulatedDelta = useRef(0);

  // Upload handler
  async function handleUpload(e: React.ChangeEvent<HTMLInputElement>) {
    const file = e.target.files?.[0];
    if (!file) return;

    const formData = new FormData();
    formData.append("pdf", file);

    const res = await fetch("/api/upload", {
      method: "POST",
      body: formData,
    });

    const data = await res.json();

    if (data.success) {
      setSlides(data.slides);
      setCurrent(0);
    } else {
      alert("Upload failed: " + data.error);
    }
  }

  // Handle slide transitions
  useEffect(() => {
    isTransitioning.current = true;
    accumulatedDelta.current = 0;
    const timer = setTimeout(() => {
      isTransitioning.current = false;
    }, 500);
    return () => clearTimeout(timer);
  }, [current]);

  // MOBILE SWIPE HANDLING
  function handleTouchStart(e: React.TouchEvent) {
    touchStartY.current = e.touches[0].clientY;
  }

  function handleTouchEnd(e: React.TouchEvent) {
    const touchEndY = e.changedTouches[0].clientY;
    if (touchStartY.current == null) return;

    const delta = touchStartY.current - touchEndY;

    if (delta > 50 && current < slides.length - 1) {
      setCurrent(current + 1);
    } else if (delta < -50 && current > 0) {
      setCurrent(current - 1);
    }

    touchStartY.current = null;
  }

  // IMPROVED DESKTOP SCROLL HANDLING - prevents multi-slide jumps
  function handleWheel(e: React.WheelEvent) {
    e.preventDefault();
    
    if (isTransitioning.current) {
      return;
    }

    const now = Date.now();
    const timeSinceLastScroll = now - lastScrollTime.current;

    // Reset accumulated delta if too much time passed
    if (timeSinceLastScroll > 200) {
      accumulatedDelta.current = 0;
    }

    // Accumulate scroll delta
    accumulatedDelta.current += Math.abs(e.deltaY);
    lastScrollTime.current = now;

    // Require threshold to be met before changing slides
    const threshold = 100;

    if (accumulatedDelta.current >= threshold) {
      if (e.deltaY > 0 && current < slides.length - 1) {
        setCurrent(current + 1);
        accumulatedDelta.current = 0;
      } else if (e.deltaY < 0 && current > 0) {
        setCurrent(current - 1);
        accumulatedDelta.current = 0;
      }
    }
  }

  // Keyboard navigation
  useEffect(() => {
    function handleKeyDown(e: KeyboardEvent) {
      if (isTransitioning.current) return;
      
      if (e.key === "ArrowDown" && current < slides.length - 1) {
        setCurrent(current + 1);
      } else if (e.key === "ArrowUp" && current > 0) {
        setCurrent(current - 1);
      }
    }

    if (slides.length > 0) {
      window.addEventListener("keydown", handleKeyDown);
      return () => window.removeEventListener("keydown", handleKeyDown);
    }
  }, [current, slides.length]);

  // UPLOAD SCREEN
  if (slides.length === 0) {
    return (
      <div className="fixed inset-0 flex flex-col items-center justify-center bg-gradient-to-br from-gray-900 via-black to-gray-900 text-white">
        <div className="text-center space-y-6 p-8">
          <h1 className="text-5xl font-bold bg-clip-text text-transparent bg-gradient-to-r from-purple-400 to-pink-600">
            Slide AI
          </h1>
          <p className="text-gray-400 text-base">Upload a PDF to begin your presentation</p>
          <label className="cursor-pointer inline-block">
            <input
              type="file"
              accept="application/pdf"
              onChange={handleUpload}
              className="hidden"
            />
            <div className="px-10 py-4 bg-gradient-to-r from-purple-500 to-pink-500 rounded-full hover:from-purple-600 hover:to-pink-600 transition-all duration-300 shadow-xl hover:shadow-purple-500/50 font-medium">
              Choose PDF File
            </div>
          </label>
        </div>
      </div>
    );
  }

  const slide = slides[current];

  return (
    <div
      className="fixed inset-0 bg-black text-white overflow-hidden"
      onWheel={handleWheel}
      onTouchStart={handleTouchStart}
      onTouchEnd={handleTouchEnd}
    >
      {/* Main slide container with proper sizing */}
      <div className="absolute inset-0 flex items-center justify-center" style={{ padding: '3vh 3vw' }}>
        <img
          key={current}
          src={`data:image/png;base64,${slide.image_base64}`}
          alt={`Slide ${slide.page}`}
          className="max-w-full max-h-full object-contain rounded-lg shadow-2xl"
          style={{
            animation: "slideIn 0.4s ease-out",
            maxWidth: '94vw',
            maxHeight: '88vh'
          }}
        />
      </div>

      {/* Slide counter - bottom center */}
      <div className="absolute bottom-8 left-1/2 transform -translate-x-1/2 bg-black/60 backdrop-blur-md px-5 py-2.5 rounded-full text-sm font-medium shadow-lg">
        {current + 1} / {slides.length}
      </div>

      {/* Navigation arrows - sides */}
      {current > 0 && (
        <button
          onClick={() => !isTransitioning.current && setCurrent(current - 1)}
          className="absolute left-6 top-1/2 transform -translate-y-1/2 w-14 h-14 bg-white/10 backdrop-blur-md hover:bg-white/20 rounded-full flex items-center justify-center transition-all duration-200 opacity-0 hover:opacity-100 group shadow-lg"
          aria-label="Previous slide"
        >
          <svg className="w-7 h-7" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M15 19l-7-7 7-7" />
          </svg>
        </button>
      )}

      {current < slides.length - 1 && (
        <button
          onClick={() => !isTransitioning.current && setCurrent(current + 1)}
          className="absolute right-6 top-1/2 transform -translate-y-1/2 w-14 h-14 bg-white/10 backdrop-blur-md hover:bg-white/20 rounded-full flex items-center justify-center transition-all duration-200 opacity-0 hover:opacity-100 group shadow-lg"
          aria-label="Next slide"
        >
          <svg className="w-7 h-7" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2.5} d="M9 5l7 7-7 7" />
          </svg>
        </button>
      )}

      {/* Progress indicator - right side */}
      <div className="absolute right-8 top-1/2 transform -translate-y-1/2 flex flex-col gap-2.5">
        {slides.map((_, idx) => (
          <button
            key={idx}
            onClick={() => !isTransitioning.current && setCurrent(idx)}
            className={`w-1.5 rounded-full transition-all duration-300 cursor-pointer ${
              idx === current
                ? "bg-white h-12"
                : idx < current
                ? "bg-white/40 h-8 hover:bg-white/60"
                : "bg-white/20 h-8 hover:bg-white/40"
            }`}
            aria-label={`Go to slide ${idx + 1}`}
          />
        ))}
      </div>

      {/* Instructions - top */}
      <div className="absolute top-8 left-1/2 transform -translate-x-1/2 text-center bg-black/40 backdrop-blur-md px-4 py-2 rounded-full">
        <div className="text-xs opacity-70">Scroll, swipe, or use ↑↓ keys</div>
      </div>

      <style jsx>{`
        @keyframes slideIn {
          from {
            opacity: 0;
            transform: translateY(20px) scale(0.97);
          }
          to {
            opacity: 1;
            transform: translateY(0) scale(1);
          }
        }
      `}</style>
    </div>
  );
}